import * as React from 'react'
import { Button, Dialog, Layout } from '../auto-ui'

class Demo extends React.PureComponent {
  state = { visible: false }

  render() {
    return (
      <Layout>
        <Layout.Body>
          <Button onClick={this.onBtnClick}>Dialog</Button>
          <Dialog visible={this.state.visible}>
            <h1></h1>
            <p></p>
            <p>dialog</p>
            <br />
            <Button onClick={this.onBtnClick}></Button>
          </Dialog>
        </Layout.Body>
      </Layout>
    )
  }

  private onBtnClick = () => {
    this.setState({ visible: !this.state.visible })
  }
}

export default Demo
