import * as React from 'react'
import { Alert, Button, Layout } from '../auto-ui'

const Demo: React.FC<any> = () => {
  const onClickA = () => {
    Alert({
      title: '',
      desc: '',
      btns: [{ name: '', type: 'primary', hollow: true }, { name: '', type: 'primary' }],
    })
  }

  const onClickB = () => {
    Alert({
      title: '',
      desc: '',
      btnDirection: 'column',
      btns: [{ name: '', type: 'primary', hollow: true }, { name: '', type: 'primary' }],
    })
  }

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClickA}></Button>
        <br />
        <Button onClick={onClickB}></Button>
      </Layout.Body>
    </Layout>
  )
}

export default Demo
