import * as React from 'react'
import { Button, Layout, Popup } from '../auto-ui'

class Demo extends React.PureComponent {
  state = { visible: false }

  render() {
    return (
      <Layout>
        <Layout.Body>
          <Button onClick={this.onBtnClick}>Popup</Button>
          <Popup smoothBorder={true} visible={this.state.visible}>
            <p>Popup</p>
            <br />
            <Button onClick={this.onBtnClick}></Button>
          </Popup>
        </Layout.Body>
      </Layout>
    )
  }

  private onBtnClick = () => {
    this.setState({ visible: !this.state.visible })
  }
}

export default Demo
