import * as React from 'react'
import { Button, Layout, Toast } from '../auto-ui'

const Demo: React.FC<any> = () => {
  const onClick = () => {
    Toast('3', 10000000)
    setTimeout(() => {
      Toast.hide()
    }, 3000)
  }

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClick}></Button>
      </Layout.Body>
    </Layout>
  )
}

export default Demo
