import * as React from 'react'
import { Button, Layout, Popup } from '../auto-ui'

class Demo extends React.PureComponent {
  state = { visible: false, height: 30 }

  render() {
    return (
      <Layout>
        <Layout.Body>
          <Button onClick={this.onBtnClick.bind(null, 30)}>30%Popup</Button>
          <br />
          <Button onClick={this.onBtnClick.bind(null, 100)}>100%Popup</Button>
          <Popup height={this.state.height} visible={this.state.visible}>
            <h1></h1>
            <p></p>
            <br />
            <Button onClick={this.onBtnClick.bind(null, this.state.height)}></Button>
          </Popup>
        </Layout.Body>
      </Layout>
    )
  }

  private onBtnClick = (height: number) => {
    this.setState({ visible: !this.state.visible, height })
  }
}

export default Demo
